<template>
  <Card class="card">
    <Breadcrumb>
      <BreadcrumbItem>港口管理</BreadcrumbItem>
      <BreadcrumbItem>修改港口信息</BreadcrumbItem>
    </Breadcrumb>
    <div class="box-container">
      <Collapse v-model="value1" accordion>
        <Panel name="1">
           <span>港口基本信息</span>
          <Row slot="content" class="row">
              <Col span="3">港口</Col>
              <Col span="6">{{result.port ? result.port:"-"}}</Col>
              <Col span="3">港口使用费</Col>
              <Col span="6"> <Input style="width:220px" placeholder="请输入港口使用费"  v-model="portFee" /> </Col>
          </Row>
          <Row slot="content" class="row">
              <Col span="3">吃水</Col>
              <Col span="6"><Input style="width:220px" placeholder="请输入吃水"  v-model="freight" /> </Col>
              <Col span="3">港口作业区</Col>
              <Col span="6"><Input style="width:220px" placeholder="请输入港口作业区" v-model="workArea" /> </Col>
          </Row>
           <Row slot="content" class="row">
              <Col span="3">船长限制</Col>
              <Col span="6"><Input style="width:220px" placeholder="请输入船长限制" v-model="long" /> </Col>
              <Col span="3">船宽限制</Col>
              <Col span="6"><Input style="width:220px" placeholder="请输入船宽限制" v-model="width" /> </Col>
          </Row>
           <Row slot="content" class="row">
              <Col span="3">封航状态</Col>
              <Col span="6">
                <ButtonGroup>
                  <Button @click="tab(0)" v-bind:type='lineStatus==0?"primary" :"default" ' >是</Button>
                  <Button @click="tab(1)" v-bind:type='lineStatus==1?"primary" :"default" ' >否</Button>
                </ButtonGroup>
            </Col>
          </Row>
           <Row slot="content" class="row">
              <Col span="3">封航/停止作业开始时间</Col>
              <Col span="6"> <DatePicker type="date" placeholder="封航/停止作业开始时间" style="width: 220px" format="yyyy/MM/dd"  v-model="starTime" @on-change='dateChange($event,1)'  /></Col>
              <Col span="3">封航/停止作业结束时间</Col>
              <Col span="6"><DatePicker type="date" placeholder="封航/停止作业结束时间" style="width: 220px" format="yyyy/MM/dd" v-model="endTime" @on-change="dateChange($event,2)" /></Col>
          </Row>

           <Row slot="content" class="row">
              <Col span="3">封航/停止作业原因</Col>
              <Col span="6"><Input style="width:220px" placeholder="请输入封航原因" v-model="reason"/> </Col>
          </Row>
          <Row slot="content" class="row">
            <Divider/>
          </Row>
           <Row slot="content" class="row">
              <Button type="primary" @click="save">保存</Button>
          </Row>
        </Panel>
      </Collapse>
    </div>
  </Card>
</template>
<script>
   export default {
        data () {
            return {
                value1: '1',
                value2:'2',
                result:'',
                freight:'',
                portFee:'',
                workArea:'',
                long:'',
                width:'',
                starTime:'',
                endTime:'',
                reason:'',
                lineStatus:0,
            }
        },
        // watch:{
        //   starTime(){

        //   },
        // },
         methods:{
          async  portDetail(){
              let data  = {
                portCode:this.$route.query.code
              }
              try{
                  let res = await this.$http.portDetail(data)
                  if(0==res.data.status ){
                    this.result = res.data.result
                    this.freight = res.data.result.shipDraught
                    this.width = res.data.result.shipBreadth
                    this.long = res.data.result.shipLength
                    this.portFee = res.data.result.usageFee
                    this.reason = res.data.result.stopReason
                    this.starTime = res.data.result.startDate ? res.data.result.startDate.slice(0,10):""
                    this.endTime = res.data.result.endDate ? res.data.result.endDate.slice(0,10):""
                    this.lineStatus = res.data.result.stopStatus
                    this.workArea = res.data.result.polygnIndex
                  }
              }
              catch(err){
                this.$Notice.error({
                  desc:'服务器错误,请稍后重试.'
                })
                throw Error(err)

              }
           },
          //  格式化时间
          dateFormat(val) {
                let year = val.getFullYear().toString();
                let month = val.getMonth() >= 9
                    ? (val.getMonth() + 1).toString()
                    : "0" + (val.getMonth() + 1);
                let date = val.getDate() >= 9
                    ? val.getDate().toString()
                    : "0" + val.getDate();
                return year + "/" + month + "/" + date;
          },
         async save(){
           let check = new Date(this.starTime).getTime() <= new Date(this.endTime).getTime()
           let starTime = this.starTime.toLocaleString().includes(' ') ? this.starTime.toLocaleString().slice(0,10) : this.starTime
           let endTime = this.endTime.toLocaleString().includes(' ') ? this.endTime.toLocaleString().slice(0,10) : this.endTime
           if(this.starTime == '' && this.endTime == ''){
             check = true
           }

          if(check){
            let data = {
                portCode:this.$route.query.code,
                shipDraught:this.freight,
                shipBreadth:this.width,
                shipLength:this.long,
                portUsageFee:this.portFee,
                stopReason:this.reason,
                startDate:starTime,
                endDate:endTime,
                polygn:this.workArea,
                stopStatus:this.lineStatus,
            }
           try{

                  let res = await this.$http.updatePort(data)
                   if(!res.data.status){
                    this.$Notice.success({
                        desc:"保存成功"
                    })
                    setTimeout(()=>{
                        const {href} = this.$router.resolve({
                            path:'/portCenter/portdetail',
                            query:{
                                code:this.$route.query.code
                            }
                         });
                         window.open(href, '_blank')
                    },1300)
                   }
                   else{
                        this.$Notice.error({
                        desc:res.data.msg
                    })
                    return false
                   }
               }
               catch(err){
                    this.$Notice.error({
                    desc:'服务器错误,请稍后重试.'
                })
                console.log(err)
               }

          }
          else{
            this.$Notice.error({
              desc:'开始时间不能大于结束时间'
            })
            return
          }

           },
           tab(e){
               this.lineStatus = e
           },
          dateChange(e,type){
               if(1==type){
                   this.starTime = e
               }
               else{
                   this.endTime = e
               }
           },
        },
        created(){
          this.portDetail()
        }
    }
</script>
<style lang="scss" scoped>
.table-row{
  margin: 20px 0px;
}
.ivu-row{
    padding: 0 30px;
    margin: 10px 0;
}

</style>



